/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 13px;
  font-family: "Barlow Semi Condensed", sans-serif;
  color: hsl(0deg, 0%, 100%);
}

body {
  background-color: hsl(210deg, 46%, 95%);
  display: flex;
  justify-content: center;
}

main {
  width: calc(100% - 48px);
  margin: 10vh 0;
  display: grid;
  gap: 25px;
}
main .card {
  background-color: red;
  border-radius: 8px;
  padding: 2rem 2.4rem;
  position: relative;
}
main .card .profile-wrapper {
  display: flex;
  align-items: center;
  gap: 1.3rem;
  margin-bottom: 1rem;
}
main .card .profile-wrapper img {
  border-radius: 50%;
  width: 2.5rem;
  height: 2.5rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
}
main .card .profile-wrapper h3 {
  font-size: 1rem;
  font-weight: 600;
}
main .card .profile-wrapper p {
  font-size: 0.9rem;
  font-weight: 500;
}
main .card h2 {
  font-size: 1.56rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
}
main .card p {
  font-size: 1.1rem;
  font-weight: 500;
  line-height: 1.4rem;
}
main .dark {
  color: hsl(0deg, 0%, 100%);
}
main .dark p {
  color: rgba(255, 255, 255, 0.6);
}
main .light {
  color: hsl(217deg, 19%, 35%);
}
main .light p {
  color: rgba(72, 85, 106, 0.6);
}
main #card1 {
  background-color: hsl(263deg, 55%, 52%);
  background-image: url(../img/bg-pattern-quotation.svg);
  background-repeat: no-repeat;
  background-position: top right 25px;
}
main #card2 {
  background-color: hsl(217deg, 19%, 35%);
}
main #card3 {
  background-color: hsl(0deg, 0%, 100%);
}
main #card4 {
  background-color: hsl(219deg, 29%, 14%);
}
main #card5 {
  background-color: hsl(0deg, 0%, 100%);
}